section {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0;
  padding: 0;
}

.survey-logo {
  width: 50%;
  height: 10%;
}

.one {
  background-color: white;
}

.two {
  background-color: #f6a333;
}

.question-section {
  background-color: #f1f2f3;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.question-card {
  width: 500px;
  height: 250px;
  border-radius: 10px;
  background-color: #fff;
}

.btn-primary {
  background: #fff;
  box-sizing: border-box;
  border-radius: 6px;
  height: 3rem;
  display: flex;
  align-items: center;
  border-width: 0;
  width: 25vh;
  background-color: #fff;
  width: 160px;
  padding: 20px;
  color: #fff;
  cursor: pointer;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: box-shadow, transform;
  transition-property: box-shadow, transform;
  justify-content: center;
  align-items: center;
}

.btn span {
  font-family: Diodrum, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Helvetica Neue, Arial, sans-serif;
  color: black;
  text-align: center;
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: bold;
}
.btn:hover,
.btn:focus,
.btn:active {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

