.choice-card {
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.2);
  background-color: #fff;
  box-sizing: border-box;
}

.button-container {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  padding-left: 10em;
  padding-right: 10em;
  padding-bottom: 5em;
}
